.i_DKYG_modal:empty {
  display: none;
}

.i_DKYG_overlay {
  z-index: 10;
  background-color: #11111198;
  position: absolute;
  inset: 0;
}

.i_DKYG_inner {
  z-index: 20;
  border-radius: var(--radius);
  border: 1px solid var(--color-grey-200);
  box-shadow: 0 1px 2px var(--color-grey-200);
  background-color: var(--color-black);
  width: 350px;
  padding: 14px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.i_DKYG_inner :first-child {
  margin-top: 0;
}

.i_DKYG_footer {
  border-top: 1px solid var(--color-grey-200);
  justify-content: flex-end;
  align-items: center;
  margin-top: 14px;
  padding-top: 14px;
  display: flex;
}

.i_DKYG_noScroll {
  overflow: hidden;
}

